 .banner{
     height: 448px;
     position: relative;
     color: white;
     object-fit: contain;

 }
 .banner_contents{
     margin-left: 30px;
     padding-top: 140px;
     height: 190px;
 }
 .banner_title{
     font-size: 3rem;
     font-weight: 800;
     padding-bottom: 0.3rem;
 }
 .banner_description{
     width: 45rem;;
     line-height: 1.3;
     padding-top: 1rem;
     font-size: 0.8rem;
     height: 80px;
     max-width: 360px;
 }
 .banner_fadebottom{
     /* height: 7.4rem; */
     height: 7.7rem;
    margin-left: -30px;
     background-image: linear-gradient(
         180deg,
         transparent,
         rgba(37 ,37 ,37 ,0.61),
         #111
     );

 }
 .banner_button{
     cursor: pointer;
     border: none;
     outline: none;
     color: #fff;
     font-weight: 700;
     padding-left: 2rem;
     padding-right: 2rem;
     border-radius: 0.2vw;
     margin-right: 1rem;
     padding-top: 0.5rem;
     padding-bottom: 0.5rem;
     background-color: rgba(51 ,51,51,0.5);
    }
    .banner_button:hover{
        background-color: #e6e6e6;
        color: #000;
        transition: all 0.2sec;
    }