.profileScreen{
    height: 100vh;
    color: white;
}
.profileScreen_body{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    max-width: 800px;

}
.profileScreen_info{
    display: flex;

}
.profileScreen_body>h1{
    font-size: 60px;
    font-weight: 400;
    margin-bottom: 20px;
    border-bottom: 1px solid #282c2d;
}
.profileScreen_info>img{
    height: 100px;

}
.profileScreen_details{
    color: white;
    flex: 1px;
    margin-left: 25px;
}
.profileScreen_details>h2{
background-color: grey;
additive-symbols: 15px;
font-size: 15px;
padding-left: 20px;

}
.profileScree_signOut{
    cursor: pointer;
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    color: #fff;
    font-weight: 600;
    background-color: #e50914;
    border: none;
}
.profileScreen_plans{
    margin-top: 20px;
}
.profileScreen_plans > h3 {
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;



}