.signupScreen{
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0, 0 , 0,0.85);
}
.signupScreen > form{
    display: grid;
    flex-direction: column;
}
.signupScreen> form >input{
outline-width: 0;
height: 40px;
margin-bottom: 14px;
border-radius: 5px;
border: none;
padding: 5px 15px;
}
.signupScreen>form>button{
    font-size: 1rem;
    border-radius: 5px;
    color: #fff;
    padding: 16px 20px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}
.signupScreen>form>h1{
    text-align: left;
    margin-bottom: 25px;
}
.signupScreen>form>h4{
    text-align: left;
    margin-top: 30px;
}
.signupScreen_gray{
    color: grey;
}
.signupScreen_link:hover{
    cursor: pointer;
    text-decoration: underline;
}